body {
  background-color: #293b29;
}

.clock-bg {
  width: 350px;
  height: 350px;
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  margin: auto;
  border-radius: 80px;
  background-color: #3d5a45;
  box-shadow: 0 0 10px #1f2c1f;
  z-index: 1;
}

.clock-bg .clock__body {
  width: 310px;
  height: 310px;
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  margin: auto;
  border-radius: 50%;
  background-color: #293b29;
  border: 3px solid #1f2c1f;
  z-index: 2;
}

.clock__body__deco,
.clock__body__decoTxt,
.clock__body__hand {
  z-index: 3;
  width: 260px;
  height: 260px;
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  margin: auto;
}

.clock__body__deco .hour0,
.clock__body__deco .hour30,
.clock__body__deco .hour60,
.clock__body__deco .hour90,
.clock__body__deco .hour120,
.clock__body__deco .hour150,
.clock__body__deco .min {
  width: 100%;
  height: 1px;
  position: absolute;
  top: 0;
  bottom: 0;
  margin: auto;
}

.clock__body__deco .hour0:after, .clock__body__deco .hour0:before,
.clock__body__deco .hour30:after,
.clock__body__deco .hour30:before,
.clock__body__deco .hour60:after,
.clock__body__deco .hour60:before,
.clock__body__deco .hour90:after,
.clock__body__deco .hour90:before,
.clock__body__deco .hour120:after,
.clock__body__deco .hour120:before,
.clock__body__deco .hour150:after,
.clock__body__deco .hour150:before,
.clock__body__deco .min:after,
.clock__body__deco .min:before {
  content: "";
  height: 100%;
  width: 20px;
  background-color: #ff7600;
  position: absolute;
  top: 0;
}

.clock__body__deco .hour0:after,
.clock__body__deco .hour30:after,
.clock__body__deco .hour60:after,
.clock__body__deco .hour90:after,
.clock__body__deco .hour120:after,
.clock__body__deco .hour150:after,
.clock__body__deco .min:after {
  left: 0;
}

.clock__body__deco .hour0::before,
.clock__body__deco .hour30::before,
.clock__body__deco .hour60::before,
.clock__body__deco .hour90::before,
.clock__body__deco .hour120::before,
.clock__body__deco .hour150::before,
.clock__body__deco .min::before {
  right: 0;
}

.clock__body__deco .min:after,
.clock__body__deco .min:before {
  width: 2px;
  height: 2px;
  top: 0;
  bottom: 0;
  margin: auto;
  background-color: #ffffff;
  border-radius: 50%;
}

.clock__body__deco .min:after {
  transform: translateX(50%);
  left: auto;
  right: 10px;
}

.clock__body__deco .min:before {
  transform: translateX(-50%);
  right: auto;
  left: 10px;
}

.clock__body__deco .hour0 {
  transform: rotateZ(90deg);
}

.clock__body__deco .hour30 {
  transform: rotateZ(60deg);
}

.clock__body__deco .hour60 {
